  overflow: hidden;
  padding: 0.25rem;
  border-radius: var(--border-radius-400);
}

.recipe-card__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: var(--border-radius-300);
}

.recipe-card__description {
  display: flex;
  flex-direction: column;
  row-gap: 1rem;
  padding: 0.5rem 1rem;
}

.recipe-card__title {
  font: var(--font-type-h3);
  color: var(--color-text-secondary);
}

.recipe-card__cooking-time-value {
  font-weight: 700;
}

/* -------------------------------------------------------------------------- */
/*                                   RECIPE                                   */
/* -------------------------------------------------------------------------- */
.recipe {
  position: fixed;
  inset: 0;
  overflow: auto;
  background: var(--color-bg-primary);
  transform: translateX(100%);
  transition: transform 0.3s;
}
.recipe_show {
  transform: translateX(0%);
}

.recipe-content {
  width: 100%;
  height: 100%;
  min-height: 120vh;
}

.recipe__header {
  position: relative;
}

.recipe__close-button {
  display: grid;
  place-items: center;
  position: absolute;
  top: 10px;
  left: 10px;
  width: 35px;
  height: 35px;
  border: none;
  background-color: var(--color-bg-primary);
  border-radius: 50%;
  color: inherit;
  font: inherit;
}
.recipe__close-button svg {
  width: 1rem;
  fill: #fff;
}

.recipe__img-wrapper {
  height: 60vw;
  overflow: hidden;
  box-shadow: inset 0 0 20px 10px rgba(0, 0, 0, 0.6);
}

.recipe__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  position: relative;
  z-index: -1;
}

.recipe__body {
