  top: -1rem;
  padding: 1rem;
  background: var(--color-bg-primary);
  border-radius: var(--border-radius-400);
}

.recipe__title {
  font: var(--font-type-h2);
}

.recipe__meta {
  margin: 0;
  margin-top: 1rem;
  padding: 0;
  text-indent: 0;
  list-style-type: none;
}

.recipe__meta-item {
  display: flex;
  align-items: center;
  gap: 0.75rem;
}

.recipe__clock-icon {
  fill: #fff;
}

.recipe__user-icon {
  fill: #fff;
}

.recipe__src {
  margin-top: 0.5rem;
}

.recipe__src-link:visited {
  color: var(--color-text-primary);
}

.recipe__ingredients {
  margin-top: 1rem;
}

.recipe__ingredients-title {
  font: var(--font-type-h3);
}

.recipe__ingredients-list {
  margin: 0;
  margin-top: 0.5rem;
  padding: 0;
  text-indent: 0;
  list-style-type: none;
}

.recipe__ingredient {
  display: flex;
  justify-content: space-between;
  position: relative;
  padding-left: 1.25rem;
  font: var(--font-type-body);
  color: var(--color-text-secondary);
}
.recipe__ingredient::before {
  content: "+";
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  margin: auto 0;
}
.recipe__ingredient_used {
  color: var(--color-text-primary);
}
.recipe__ingredient_used::before {
  content: "";
  left: 1px;
  top: 3px;
  width: 7px;
  height: 7px;
  background: #fff;
  border-radius: 50%;
}

.recipe__instructions {
  margin-top: 1rem;
}

.recipe__instructions-title {
  font: var(--font-type-h3);
}

.recipe__instruction-steps-list {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
  margin: 0;
  margin-top: 0.5rem;
  padding: 0;
  text-indent: 0;
  list-style-type: none;
}

.instruction-step {
  display: grid;
  grid-template-columns: 1.5rem 1fr;
  font: var(--font-type-body);
  line-height: 1.5;
}

.instruction-step__step {
  margin: 0;
}

/* -------------------------------------------------------------------------- */
/*                                   FILTERS                                  */
/* -------------------------------------------------------------------------- */
.filters__button {
  display: grid;
  place-items: center;
  height: 42px;
  aspect-ratio: 1/1;
  border: 0;
  padding: 0;
  background: var(--color-bg-secondary);
  border-radius: var(--border-radius-200);
}

.filters__button-svg * {
  fill: #fff;
}

.filters {
  position: relative;
  padding: 1.5rem 0;
}

.filters__body {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  background: var(--color-bg-secondary);
  padding: 0 1.5rem;
}

/* -------------------------------------------------------------------------- */
/*                             POPULAR INGREDIENTS                            */
/* -------------------------------------------------------------------------- */
.popular-ingredients__title {
  font: var(--font-type-h3);
  color: var(--color-text-secondary);
}

.popular-ingredients__list {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  padding: 0;
  margin: 0;
  margin-top: 0.75rem;
  text-indent: 0;
  list-style-type: none;
}

.popular-ingredients__list-item {
  display: flex;
  align-items: center;
  padding: 0.1rem 0.7rem;
  background: var(--color-bg-tertiary);
  border-radius: var(--border-radius-300);
}
.popular-ingredients__list-item_selected {
  background: var(--color-bg-highlight);
  color: var(--color-text-highlight);
}

body {
  font: var(--font-type-body);
  color: var(--color-text-primary);
}

.prevent-scrolling {
  overflow: hidden;
}

.main {
  min-height: 100svh;
  background: var(--color-bg-primary);
}

.wrapper {
  width: 100%;
  max-width: 1280px;
  margin: 0 auto;
